body {
	background: rgb(255, 204, 51);
}
.jumbotron {
	background: rgb(0, 51, 153);
}

#basketball {
	width: 100px;
	height: 100px;
}
.playerCaption {
	text-align: center;
	width: 100%;
	position: relative;
	bottom: 1.5em;
	color: white;
	text-shadow: 0px 0px 3px black;
}

.playerContainer:hover {
	-webkit-transform: scale(1.1, 1.1);
	        transform: scale(1.1, 1.1);
}

.playerImage {
	max-width: 100%;
}

.playerNumber {
	color: rgb(0, 51, 153);
	text-align: right;
	position: relative;
	top: 2em;
}
#playButton {
	background: rgb(0, 51, 153);
	border-color: rgb(0, 34, 102);
	color: white;
}

#playButton:hover {
	background: rgb(0, 77, 230);
}

#playButton:active {
	background: rgb(4, 12, 27);
}
#footer {
	background: rgb(0, 51, 153);
	color: grey;
}

